@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Reset css */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family:'Montserrat', Arial, Helvetica, sans-serif;
}

:root {
  --color-dark-blue: #202040;
  --color-medium-blue: #404081;
  --color-light-blue: #40bec7;
  --color-light-red: #ff6363;
  --color-light-gray: #eaeaea;
  --color-light-violet: #95a4ff;
  --fs-large: 2.5rem;
  --fs-medium: 1.8rem;
  --fs-small: 1.6rem;
}

body {
  background-color: #202040;
  background-color: var(--color-dark-blue);
  color: #eaeaea;
  color: var(--color-light-gray);
  font-size: 1.6rem;
  font-size: var(--fs-small);
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  width: 100%;
  padding: 3rem 0;
  display: flex;
  justify-content: center;
  text-align: center;
  border-bottom: 1px solid var(--color-light-gray);
}
nav {
  width: 40%;
}
.main-nav {
  display: flex;
  justify-content: space-between;
  list-style: none;
}
h1.logo {
  font-size: 4rem;
}
a {
  text-decoration: none;
  color: inherit;
  transition:all 0.6s ease;
}
a:hover {
  color: var(--color-light-blue);
}
span.user_name {
  color: var(--color-light-blue);
}


input {
  display: block;
  width: 100%;
  font-size: 14pt;
  line-height: 28pt;
  margin-bottom: 14pt;
  border: none;
  border-bottom: 5px solid black;
  background: white ;
  padding-left: 5px;
  outline: none;
  color: black;
}
input:focus {
  border-bottom: 5px solid var(--color-light-blue);
}
input.search_input {
  max-width: 300px;
}
button {
  display: block;
  margin: 0 auto;
  line-height: 28pt;  
  padding: 0 16px;
  background: var(--color-light-blue);
  letter-spacing: 2px;
  transition: 0.2s all ease-in-out;
  outline: none;
  border: 1px solid black;
  box-shadow: 3px 3px 1px #95a4ff, 3px 3px 1px 1px black;
}
button:hover {
  background: white;
  color: black;
  border: 1px solid var(--color-light-blue);
  cursor: pointer;
}
::selection {
  background: #ffc8ff;
}
.btn_search {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: 20px;
}
.Btn_Del {
  background-color: var(--color-light-red);
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  border-bottom: 5px solid #95a4ff;
  -webkit-text-fill-color: #2A293E;
  -webkit-box-shadow: 0 0 0px 1000px #f8f4e5 inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}
.task-list {
  border-top: 1px solid #3C1C78;
}
.task-list__form {
  margin-top: 3rem;
  margin-bottom: 5rem;
  background: var(--color-medium-blue);
  padding: 50px 100px;
  border: 2px solid white;
  box-shadow: 15px 15px 1px var(--color-light-blue), 15px 15px 1px 2px var(--color-light-blue);
}
.controllTask {
  display: flex;
  justify-content:space-between;
}
.searchTask {
  display: flex;
}
.custom-select {
  position: relative;
  display: block;
  max-width: 400px;
  min-width: 180px;
  margin: 0 auto;
  border: 1px solid #3C1C78;
  background-color: white;
  z-index: 10;
}
.custom-select select {
  border: none;
  outline: none;
  background: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  margin: 0;
  display: block;
  width: 100%;
  padding: 12px 55px 15px 15px;
  font-size: 14px;
  color: var(--color-medium-blue);
}
.custom-select:after {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 100%;
  line-height: 38px;
  content: "âˆ¨";
  text-align: center;
  color: var(--color-medium-blue);
  font-size: 24px;
  border-left: 1px solid #3C1C78;
  z-index: -1;
}

body {
  margin-top: 80px;
  background-color: #1A004A;
}

/* list */
table {
  width: 100%;
  margin: 25px auto;
  border-collapse: collapse;
  border: 1px solid #eee;
  border-bottom: 2px solid #00cccc;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1), 0px 10px 20px rgba(0, 0, 0, 0.05), 0px 20px 20px rgba(0, 0, 0, 0.05), 0px 30px 20px rgba(0, 0, 0, 0.05);
}
table tr:hover td {
  color: var(--color-light-blue);
}
table th, table td {
  color: white;
  border: 1px solid #eee;
  padding: 12px 35px;
  border-collapse: collapse;
}
table th {
  background: #00cccc;
  color: #fff;
  text-transform: uppercase;
  font-size: 12px;
}
table th.last {
  border-right: none;
}
.btnControll {
  display: flex;
}
.quickSearch_input {
  font-size: inherit;
  margin: 0;
}

.task-item{

}
.task-item.completed{
  text-decoration: line-through;
  color: #666;
}

